.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(62, 36, 14);
  color: white;
  padding: 1px 40px;
  position: sticky;
  top: 0;
  z-index: 1000;
  /* font-family: 'La Belle Aurore', cursive; */
}

.nav-links a {
  color: white;
  margin-left: 20px;
  text-decoration: none;
  font-weight: bold;
  font-family: 'Times New Roman', Times, serif;
  font-size: 25px;
}

.nav-links a:hover {
  text-decoration: underline;
}

.nav-title {
  font-size: 35px;
  font-weight: "bold";
  font-family: 'La Belle Aurore', cursive;
}

.logout-btn {
  display: inline-block;
  background: none;
  border: none;
  color: white;
  margin-left: 20px;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bold;
  font-family: "Courier", sans-serif;
  cursor: pointer;
  transition: color 0.2s;
}

.logout-btn a:hover {
  text-decoration: underline;
}
