.home {
  text-align: center;
  padding: 50px;
  font-family: Arial, Helvetica, sans-serif;
  background-image: url("frontpage.PNG");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: white;
  /* display: flex; */
  /* justify-content: center;
  align-items: center; */
}

.title {
  background-color: rgba(255, 255, 255, 0.549);
}

.home-buttons {
  margin-top: 20px;
}

.home-buttons button {
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.book-preview {
  margin-top: 20px;
}

.book-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}

.book-card {
  padding-left: 20px;
  width: 200px;
  text-align: center;
}

.book-cover {
  width: 100%;
  border-radius: 6px;
}

body {
  background-image: url("pages.jpeg");
  /* cursor: url('cursor.png') 0 0, auto; */
}

* {
  cursor: url("cursor.png") 0 0, auto;
}

.title {
  overflow: hidden;
  border-right: none, solid #654321;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.05em;
  animation: typing 3s steps(30) forwards, blink-caret 0.75s step-end 12s;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: 55px;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 40%;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  25% {
    border-color: #654321;
  }
}

.welcome-in {
  font-size: 45px;
  /* margin-top: 3px; */
  margin: 0 auto;
  /* font-family: Verdana, Geneva, Tahoma, sans-serif; */
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.short {
  margin-top: 5px;
  font-size: 35px;
}
